.navbar {
  background-color: var(--skinny);
  height: max-content;
  display: flex;
  flex-direction: row;
  align-items: center;
  padding: 0 3rem;
  position: relative;
}
.navbarOptions {
  display: flex;
  flex-direction: row;
  align-items: center;
  padding: 1rem 3rem;
  margin: 0;
}
.navbarOptions li > a {
  margin-right: 1.6rem;
  cursor: pointer;
  color: #007ea7;
  transition: all ease-in-out 300ms;
  display: flex;
  align-items: center;
}
.navbarOptions li > a:hover {
  color: var(--lightGreen);
}
.optionsBox {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
#navLogo {
  width: 140px;
  cursor: pointer;
}
.extraOption {
  display: flex;
  align-items: center;
}
.extraOption svg {
  color: #007ea7;
  font-size: 2rem;
  margin-left: 20px;
  cursor: pointer;
}
.profileDroper {
  z-index: 1;
}
.profileDroper button {
  background-color: transparent;
  min-width: auto;
  padding: 0;
}
.profileDroper button:hover {
  background-color: transparent !important;
}
.menues-nav svg {
  color: rgba(27, 27, 27, 0.852);
  padding: 0;
  margin: 0 9px 0 0;
  width: max-content;
  font-size: 1.3rem;
  font-weight: 500;
}
.menues-nav li {
  color: rgba(27, 27, 27, 0.852);
  font-weight: 500;
}
.userinfo {
  padding: 0rem 1rem 0.8rem 1rem;
  background-color: var(--lightGreen2);
  border-radius: inherit;
}
.userinfo p {
  margin: 0;
  padding: 0.5rem 0 0.2rem 0;
  font-weight: 400;
  color: #007ea7;
}